.page {
  width: 100vw;
  background-color: rgb(251, 244, 233);
}
.mybtn {
  background-color: orange;
  padding: 10px 14px;
  color: aliceblue;
  margin-right: 30px;
  margin-bottom: 50px;
  border: none;
  border-radius: 5px;
  pointer-events: painted;
  cursor: pointer;
}
.allBtn {
  margin: auto;
}
.cards {
  justify-content: center;
  width: 90%;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}
.pageTitle {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
