.img {
  width: 100px;
  height: 100px;
}
.myCard {
  max-width: 300px;
  flex-grow: 1;
  border-radius: 20px;
  width: 250px;
  background-color: antiquewhite;
  margin-bottom: 30px;
  overflow: hidden;
}
.myCard img {
  overflow: hidden;
  width: 100%;
  height: 200px;
}
.cardText {
  padding: 8px;
}
.cardTitle {
  font-size: 30px;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}
.cardPrice {
  margin-block: 10px;
  padding: 5px;
  background-color: rgb(236, 185, 124);
  border-radius: 10px;
  width: fit-content;
}
.cardCategory {
  opacity: 0.8;
}
.cardDesc {
  opacity: 0.8;
  margin-top: 10px;
}
